body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.App{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}

nav ul{
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  height: 8vh;
}

nav ul>li{
  -webkit-flex-basis: 25%;
          flex-basis: 25%;
}

li.main{
  background-color: rgb(66, 134, 244);
}

li.all{
  background-color: #EA4335;
}

li.find{
  background-color: #FBBC05;
}

li.game{
  background-color: #34A853;
}

li.liked{
  background-color: #665;
}


nav ul li a{
  text-decoration: none;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.712);
  font-size: 1.3rem;
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
  box-sizing: border-box;
  padding: 5px 0;
  position: relative;
}

a span{
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)
}

article{
  -webkit-flex-grow: 1;
          flex-grow: 1;
  background-color: #1D1E22;
}

footer{
  height: 5vh;
  background-color: black;
  color: white;
  text-align: center;
  font-size: .7rem;
}

section.countryInfo{
  -webkit-flex-basis: 23%;
          flex-basis: 23%;
  overflow: hidden;
  color: rgb(156, 149, 149);
  border: 1px solid rgb(156, 149, 149);
  margin-top: 20px;
  -webkit-transition: .2s;
  transition: .2s;
  box-sizing: border-box;
  padding: 5px;
}

section.countryInfo:hover{
  background-color: #0C0D11;
}

div.country{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  
}

.country header{
  -webkit-flex-basis: 35%;
          flex-basis: 35%;
  text-align: center;
}

.country main{
  padding: 0;
  margin: 0;
}

.country header img{
  width: 50%;
  height: auto;
}

.country header h1{
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}

main div.divCountry{
  width: 100%;
  border-bottom: 1px solid rgb(156, 149, 149);
}

main div span.property{
  display: inline-block;
  width: 50%;
}

main div span.value{
  display: inline-block;
  width: 50%;
  text-align: right;
}

@media (max-width: 1100px){
  section.countryInfo{
    -webkit-flex-basis: 32%;
            flex-basis: 32%;
  }
}

@media (max-width: 800px){
  section.countryInfo{
    -webkit-flex-basis: 49%;
            flex-basis: 49%;
  }
}

@media (max-width: 550px){
  section.countryInfo{
    -webkit-flex-basis: 99%;
            flex-basis: 99%;
  }
}



main{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}



h1.loading{
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: rgb(156, 149, 149);
  font-size: 3rem;
}

section.listOfCountries{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 50%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin: 0 auto;
}

button.filterButtons{
  height: 2rem;
  margin-top: 20px;
  background-color: transparent;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

button.filterButtons:hover{
background-color: #0C0D11;
border: 1px solid #0C0D11;
}

input.amount{
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgb(156, 149, 149);
  font-size: 1.5rem;
  color: rgb(156, 149, 149);
  margin-top: 20px;
  text-align: center;
}

section.search{
  display: block;
  width: 100%;
  height: 25vh;
  text-align: center;
}

h1.searchH1{
  color: rgb(156, 149, 149);
  text-transform: uppercase;
  font-size: 3rem;
  margin: 20px 0;
  padding: 0;
}

input.searchInput{
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgb(156, 149, 149);
  font-size: 1.5rem;
  color: rgb(156, 149, 149);
}

input::-webkit-input-placeholder{
  text-align: center;
  font-size: 1rem;
}

input::-ms-input-placeholder{
  text-align: center;
  font-size: 1rem;
}

input::placeholder{
  text-align: center;
  font-size: 1rem;
}

input:focus{
  border-bottom: 1px solid #3498db;
  text-align: center;
}

button.searchButton{
  display: block;
  position:relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 20px;
  background-color: transparent;
  border: 2px solid rgb(156, 149, 149);
  font-size: 1.5rem;
  color: rgb(156, 149, 149);
  padding: 5px 10px;
  cursor: pointer;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

button.searchButton:hover{
  border: 2px solid #0C0D11;
  background-color: #0C0D11;
  color: white;
}
section.result h1{
  font-size: 1.8rem;
  color: rgb(156, 149, 149);
}

section.result span{
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 2px;
}

span.label{
  text-transform: uppercase;
  
}


button{
  background-color: #0C0D11;
  padding: 5px 10px;
  margin-left: 5px;
  border: 1px solid rgb(156, 149, 149);
  color: rgb(156, 149, 149);
  cursor: pointer;
  -webkit-transition: .2s linear;
  transition: .2s linear;
}

button.africa{
  margin: 0;
}

button.africa:hover{
  background-color: #000;
  border: 1px solid #000;
  color: white;
}

button.asia:hover{
  background-color: #F4C300;
  border: 1px solid #F4C300;
  color: white;
}

button.americas:hover{
  background-color: #DF0024;
  border: 1px solid #DF0024;
  color: white;
}

button.europe:hover{
  background-color: #0085C7;
  border: 1px solid #0085C7;
  color: white;
}

button.oceania:hover{
  background-color: #009F3D;
  border: 1px solid #009F3D;
  color: white;
}

button:disabled{
  background-color: transparent;
}

button:disabled:hover{
  background-color: transparent;
  color: rgb(156, 149, 149);
  border: 1px solid rgb(156, 149, 149);
  cursor: default;
}

main.mainGame{
  display: block;
  width: 100%;
  text-align: center;
  position: relative;
}

.mainGame h3.gameH3{
  color: rgb(156, 149, 149);
}

.mainGame form{
  width: 40%;
  margin: 0 auto;
}
.mainGame form label{
  color: rgb(156, 149, 149);
  display: inline-block;
  width: 50%;
  text-align: left;
}

.mainGame form div{
  margin-top: 10px 
}

.mainGame form input{
  display: inline-block;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #3498db;
  color: white;
  font-size: 1.2rem;
  text-align: center;
}

.mainGame form input:focus{
  background-color: #0C0D11;
}

.mainGame form button{
  margin-top: 20px;
}


h1.mainH1{
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: rgb(156, 149, 149);
}
